    public snap(resultWindow: Glue42.Windows.GDWindow, target: string | Glue42.Windows.GDWindow, direction: Glue42.Windows.RelativeDirection): Promise<Glue42.Windows.GDWindow> {
        return new Promise((resolve, reject) => {
            const sourceWindowId = resultWindow.id;
            let targetWindowId;
            const errorMessage = "Invalid target parameter - should be an object with property id or a string. Invoked for source window id:" + resultWindow.id;
            if (!target) {
                reject(errorMessage);
                return;
            }

            if (typeof target === "string") {
                targetWindowId = target;
            } else if (typeof target.id !== "undefined") {
                targetWindowId = target.id;
            } else {
                reject(errorMessage);
                return;
            }

            const args: any = {
                sourceWindowId,
                targetWindowId,
            };

            if (direction) {
                args.snappingEdge = direction;
            }
            function successHandler(w: Glue42.Windows.GDWindow) {
                resolve(w);
            }

            function errorHandler(e: string) {
                reject(e);
            }
            return this.agmAction(resultWindow, "T42.Wnd.Snap", successHandler, errorHandler, args);
        });
    }

