    private agmAction(resultWindow: Glue42.Windows.GDWindow, action: string, success?: (window: Glue42.Windows.GDWindow) => void, error?: (error: string) => void, args?: any, conditionFunc?: () => void, subscribeFunc?: any, ignoreTimeout?: boolean) {
        // Stop if the window is closed
        ignoreTimeout = ignoreTimeout || false;

        if (resultWindow.url === undefined) {
            if (typeof error === "function") {
                error("Cannot execute a command on a closed window.");
            }
            return;
        }

        if (typeof conditionFunc === "undefined" || conditionFunc()) {
            this.agmInvoke(resultWindow, action, args, success, error);
        } else {
            let removeCallback = () => {
                return;
            };

            let isResolved = false;
            const successHandler = () => {
                isResolved = true;
                removeCallback();
                if (typeof success === "function") {
                    success(resultWindow);
                }
            };

            removeCallback = subscribeFunc(successHandler);
            if (!ignoreTimeout) {
                setTimeout(() => {
                    if (!isResolved) {
                        if (typeof error === "function") {
                            error("Agm invoke timeout! action: " + action);
                        }
                    }
                }, 2000);
            }

            this.agmInvoke(resultWindow, action, args, () => {
                return;
            }, error);
        }
    }

    private agmInvoke(resultWindow: Glue42.Windows.GDWindow, action: string, args?: any, success?: (window: Glue42.Windows.GDWindow) => void, error?: (error: string) => void) {
        // Add the window ID to the arguments
        args = args || {};
        args.windowId = resultWindow.id;

        // Invoke the AGM method
        this.agm.invoke(action, args, this.agmTarget, {}, successHandler, errorHandler);

        function successHandler() {
            if (typeof success === "function") {
                success(resultWindow);
            }
        }

        function errorHandler(err: any) {
            if (typeof error === "function") {
                if (err.all_return_values && err.all_return_values.length > 0) {
                    error(err.all_return_values[0].message);
                } else {
                    error(err);
                }
            }
        }
    }
