import { Glue42Core } from "@glue42/core";
import { CallbackRegistry, UnsubscribeFunction, default as CallbackFactory } from "callback-registry";

import { Glue42 } from "../../../../glue";
import { default as windowStore } from "../../store";
import { Environment, WindowConstructorOptions, WindowEvents, WindowWrapper } from "../../types";
import windowFactory from "../../window";
import { default as executor } from "./executor";

import { CompositionChanged, WindowInfoStreamFullEvent } from "./types";
import { getWindowsByTabGroupId } from "../helpers";

export default (agm: Glue42Core.AGM.API, logger: Glue42Core.Logger.API, appManagerGetter: () => Glue42.AppManager.API, cntName?: string, wndId?: string): Promise<Environment> => {
    return new Promise<any>((resolve, reject) => {
        const _registry: CallbackRegistry = CallbackFactory();
        const _logger = logger.subLogger("hc-env");
        const waitTimeout: number = 10000;

        const _windowId: string = wndId;
        let hcEnv: Environment;
        let instance: Glue42Core.AGM.Instance | "best" | "all";

        // We have two cases here:
        // 1. when we are in HC
        // 2. when we are in Browser/Node, but we have also started  HC/GD
        if (typeof cntName !== "undefined") {
            instance = { application: "HtmlContainer." + cntName };
        } else {
            instance = "best";
        }

        const streamPromise = new Promise<void>((streamResolve, streamReject) => {
            agm.subscribe("T42.Wnd.WindowStateChanged", {
                waitTimeoutMs: waitTimeout,
                target: instance,
            })
                .then((stream: Glue42Core.AGM.Subscription) => {
                    stream.onData((streamData: any) => {
                        instance = streamData.server;
                        updateWindow(streamData.data.state, streamData.data);
                    });
                    stream.onFailed((err: any) => {
                        streamReject(err);
                    });
                    streamResolve();
                })
                .catch((err: any) => {
                    streamReject("Can not subscribe for stream T42.Wnd.WindowStateChanged. Err: " + err);
                });
        });

        const listWindowsPromise = new Promise<void>((listWndResolve, listWndReject) => {
            agm.invoke("T42.Wnd.ListWindows", {}, instance, { waitTimeoutMs: waitTimeout })
                .then((result: any) => {
                    // add the server object
                    instance = result.executed_by;
                    // populate windows
                    Object.keys(result.returned).forEach((existingWindow) => {
                        const windowInfo = result.returned[existingWindow];
                        const windowObjAndEvents: WindowWrapper = createWindow(windowInfo.windowId, windowInfo);

                        // Added to store, showing to world
                        windowStore.markReadyToShow(windowObjAndEvents.API.id);
                    });
                    listWndResolve();
                })
                .catch((e: any) => {
                    listWndReject("Can not invoke T42.Wnd.ListWindows method. Err: " + e);
                });
        });

        Promise.all([streamPromise, listWindowsPromise])
            .then(() => {
                // Init the executor, if we have container name - use it, if not get the first one from the collection
                executor.init(agm, instance);
                resolve(hcEnv);
            })
            .catch((e: any) => {
                reject("can not subscribe for stream T42.Wnd.WindowStateChanged or can not invoke T42.Wnd.ListWindows ");
            });

        function updateWindow(eventType: string, windowInfo: any) {
            // if new window handle it separately
            if (eventType === "Created") {
                const win = createWindow(windowInfo.windowId, windowInfo);
                _registry.execute("window-event", windowInfo);

                // if the window has focus, trigger got focus event
                if (windowInfo.focus) {
                    focusChanged(win.Events, win.API, true);
                }
                return;
            }

            // otherwise check if the window is in our internal collections and update its state
            const windowObjectAndEvents: WindowWrapper = windowStore.get(windowInfo.windowId);

            if (!windowObjectAndEvents) {
                _logger.error(`received update for unknown window. Stream:', ${JSON.stringify(windowInfo, null, 4)}`);
                return;
            }

            const theWindow: Glue42.Windows.GDWindow = windowObjectAndEvents.API;
            const theWindowEvents: WindowEvents = windowObjectAndEvents.Events;
            const oldFrameColor: string = theWindow.frameColor;

            // const property: string = "handle" + eventType;

            // // Try the generic handlers
            // if (theWindowEvents.hasOwnProperty(property)) {
            //     theWindowEvents[property](windowInfo);
            // }

            if (eventType === "Ready") {
                windowStore.setReadyState(windowObjectAndEvents.API.id);
            }

            if (eventType === "TitleChanged") {
                theWindowEvents.handleTitleChanged(windowInfo.windowTitle);
            }

            if (eventType === "UrlChanged") {
                windowStore.setUrlChangedState(windowObjectAndEvents.API.id);
                theWindowEvents.handleUrlChanged(windowInfo.url);
            }

            if (eventType === "WindowStyleChanged") {
                const parsedWsa = parseWindowStyleAttributes(windowInfo.windowStyleAttributes);
                theWindowEvents.handleWindowSettingsChanged(parsedWsa);
                theWindowEvents.handleVisibilityChanged(!parsedWsa.hidden);
            }

            if (eventType === "Normal" ||
                eventType === "Maximized" ||
                eventType === "Minimized" ||
                eventType === "Collapsed" ||
                eventType === "Expanded") {
                const state: string | Glue42.Windows.WindowState = eventType.toLowerCase();
                theWindowEvents.handleWindowChangeState(state);
            }

            if (eventType === "ContextUpdated") {
                theWindowEvents.handleContextUpdated(windowInfo.context);
            }

            if (eventType === "FrameButtonAdded") {
                theWindowEvents.handleFrameButtonAdded(windowInfo);
            }

            if (eventType === "FrameButtonRemoved") {
                theWindowEvents.handleFrameButtonRemoved(windowInfo.buttonId);
            }

            if (eventType === "FrameButtonClicked") {
                theWindowEvents.handleFrameButtonClicked(windowInfo);
            }

            if (eventType === "FrameSelectionChanged") {
                theWindowEvents.handleFrameSelectionChanged(windowInfo.newWindow, windowInfo.prevWindow);
            }

            if (eventType === "FrameIsLockedChanged") {
                theWindowEvents.handleFrameIsLockedChanged(windowInfo.isLocked);
            }

            if (eventType === "TabHeaderVisibilityChanged") {
                theWindowEvents.handleTabHeaderVisibilityChanged(windowInfo.tabHeaderVisible);
            }

            if (eventType === "BoundsChanged") {
                const bounds: Glue42.Windows.Bounds = {
                    height: windowInfo.height,
                    left: windowInfo.left,
                    top: windowInfo.top,
                    width: windowInfo.width,
                };

                theWindowEvents.handleBoundsChanged(bounds);
            }

            if (eventType === "FocusChanged") {
                focusChanged(theWindowEvents, theWindow, windowInfo.focus);
            }

            if (eventType === "FrameColorChanged") {
                if (oldFrameColor !== windowInfo.frameColor) {
                    theWindowEvents.handleFrameColorChanged(windowInfo.frameColor);
                    _registry.execute("frame-color-changed", theWindow);
                }
            }

            if (eventType === "FrameAttached") {
                theWindowEvents.handleFrameAttached(windowInfo.tabGroupId, windowInfo.tabHeaderVisible);
            }

            if (eventType === "TabAttached") {
                theWindowEvents.handleAttached(windowInfo.tabGroupId, windowInfo.tabHeaderVisible);
                const winsToBeNotified: { [key: string]: WindowWrapper } = getWindowsByTabGroupId(theWindow.id, windowInfo.tabGroupId);
                // Getting all windows, except current, with same tabGroupId
                Object.keys(winsToBeNotified).forEach((id: string) => {
                    const win = winsToBeNotified[id];
                    // Executing event windowAttached to all windows
                    win.Events.handleWindowAttached(theWindow);
                });
                _registry.execute("tab-attached", theWindow, windowInfo.tabGroupId, windowInfo.tabHeaderVisible);
            }

            if (eventType === "TabDettached") {
                // In the event, tabGroupId is empty, will be update on the next event FrameAttached
                const oldTabGroupId = theWindow.tabGroupId;
                theWindowEvents.handleDetached(windowInfo.tabGroupId);
                const winsToBeNotified: { [key: string]: WindowWrapper } = getWindowsByTabGroupId(theWindow.id, oldTabGroupId);
                Object.keys(winsToBeNotified).forEach((id: string) => {
                    const win = winsToBeNotified[id];
                    win.Events.handleWindowDetached(theWindow);
                });
                _registry.execute("tab-detached", theWindow, windowInfo.tabGroupId, theWindow.tabGroupId);
            }

            if (eventType === "CompositionChanged") {
                theWindowEvents.handleCompositionChanged(windowInfo.neighbours, windowInfo.groupId);
                _registry.execute("composition-changed", windowInfo);
            }

            if (eventType === "GroupHeaderVisibilityChanged") {
                theWindowEvents.handleGroupHeaderVisibilityChanged(windowInfo.groupHeaderVisible);
                _registry.execute("group-header-changed", windowInfo);
            }

            // Clear the window on close event
            if (eventType === "Closed") {
                if (windowInfo.focus) {
                    // raise lost focus event
                    focusChanged(theWindowEvents, theWindow, false);
                }
                windowStore.remove(windowObjectAndEvents);
                theWindowEvents.handleWindowClose();
            }

            // expose to external listeners
            _registry.execute("window-event", windowInfo);
        }

        function focusChanged(theWindowEvents: WindowEvents, theWindow: Glue42.Windows.GDWindow, focus: boolean) {
            theWindowEvents.handleFocusChanged(focus);
            if (focus) {
                _registry.execute("got-focus", theWindow);
            } else {
                _registry.execute("lost-focus", theWindow);
            }
        }

        function createWindow(windowId: string, args: WindowInfoStreamFullEvent): WindowWrapper {
            const windowObjAndEvents: WindowWrapper = windowFactory(windowId, mapToWindowConstructorOptions(args), executor, _logger, appManagerGetter, agm);
            windowStore.add(windowObjAndEvents);
            return windowObjAndEvents;
        }

        function open(name: string, url: string, options: any, success?: (id: string) => void, error?: (error: object | string) => void): void {

            options = options || {};
            // relativeTo fix

            if (typeof options.relativeTo !== "undefined" && typeof options.relativeTo !== "string") {
                options.relativeTo = options.relativeTo.id || "";
            }

            options.windowName = name;
            options.url = url;

            agm.invoke<{ id: string }>("T42.Html.CreateWindow", options)
                .then((message) => {
                    if (message.returned !== undefined) {
                        const id: string = message.returned.id;
                        success(id);
                    } else {
                        error("failed to execute T42.Html.CreateWindow - unknown reason");
                    }
                }).catch((e: any) => {
                    if (typeof error === "function") {
                        error(e);
                    }
                });
        }

        function tabAttached(callback: (tabWindow: Glue42.Windows.GDWindow, newTabGroupId: string, tabHeaderVisible: boolean) => void): UnsubscribeFunction {
            return _registry.add("tab-attached", callback);
        }

        function tabDetached(callback: (window: Glue42.Windows.GDWindow, tabGroupId: string, oldTabGroupId: string) => void): UnsubscribeFunction {
            return _registry.add("tab-detached", callback);
        }

        function onWindowFrameColorChanged(callback: (window: Glue42.Windows.GDWindow) => void): UnsubscribeFunction {
            return _registry.add("frame-color-changed", callback);
        }

        function onWindowGotFocus(callback: (window: Glue42.Windows.GDWindow) => void): UnsubscribeFunction {
            return _registry.add("got-focus", callback);
        }

        function onWindowLostFocus(callback: (window: Glue42.Windows.GDWindow) => void): UnsubscribeFunction {
            return _registry.add("lost-focus", callback);
        }

        function onEvent(callback: (windowInfo: any) => void): UnsubscribeFunction {
            return _registry.add("window-event", callback);
        }

        function my(): string {
            return _windowId;
        }

        function onCompositionChanged(callback: (windowInfo: CompositionChanged) => void): UnsubscribeFunction {
            return _registry.add("composition-changed", callback);
        }

        function onGroupHeaderVisibilityChanged(callback: (stream: any) => void): UnsubscribeFunction {
            return _registry.add("group-header-changed", callback);
        }

        function mapToWindowConstructorOptions(args: WindowInfoStreamFullEvent): WindowConstructorOptions {
            const _state: Glue42.Windows.WindowState = typeof args.WindowState !== "undefined" ? args.WindowState.toLowerCase() as Glue42.Windows.WindowState : "normal";
            const windowStyleAttributes: Glue42.Windows.WindowSettings = parseWindowStyleAttributes(args.windowStyleAttributes);
            return {
                name: args.windowName,
                url: args.url,
                title: args.windowTitle,
                context: args.context,
                bounds: {
                    height: args.height,
                    left: args.left,
                    top: args.top,
                    width: args.width,
                },
                focus: args.focus,
                frameColor: args.frameColor,
                groupId: args.groupId,
                isCollapsed: args.isCollapsed,
                isGroupHeaderVisible: args.isGroupHeaderVisible,
                isLocked: args.isLocked,
                isTabHeaderVisible: args.isTabHeaderVisible,
                isTabSelected: args.isTabSelected,
                isVisible: !windowStyleAttributes.hidden,
                isFocused: windowStyleAttributes.focus,
                mode: windowStyleAttributes.mode,
                settings: windowStyleAttributes,
                state: _state,
                tabGroupId: args.tabGroupId,
                neighbours: args.neighbours,
            };
        }

        function parseWindowStyleAttributes(windowStyles: Glue42.Windows.WindowSettings | string): Glue42.Windows.WindowSettings {
            if (windowStyles === undefined) {
                return {} as Glue42.Windows.WindowSettings;
            }
            if (typeof windowStyles !== "object") {
                return JSON.parse(windowStyles);
            }
            return windowStyles;
        }

        function createFlydown(targetWindowId: string, config: Glue42.Windows.FlydownOptions): Promise<Glue42.Windows.Flydown> {
            throw new Error("Not implemented");
        }

        function showPopup(targetWindowId: string, config: Glue42.Windows.PopupOptions): Promise<void> {
            throw new Error("Not implemented");
        }

        hcEnv = {
            my,
            onEvent,
            open,
            tabAttached,
            tabDetached,
            onWindowFrameColorChanged,
            executor,
            onCompositionChanged,
            onGroupHeaderVisibilityChanged,
            onWindowGotFocus,
            onWindowLostFocus,
            createFlydown,
            showPopup
        };
    });
};
